	import uniPopup from '@/components/uni-popup/uni-popup.vue'
	// import mpvuePicker from '../../../components/mpvue-picker/mpvuePicker.vue';
	// import cityData from '../../../common/city.data.js';
	import tkiFileManager from "@/components/tki-file-manager.vue"
	import {formateDate} from "@/common/util.js"
    import {getUserInfo,setUserInfo} from '../../../service.js';
	export default {
		components:{tkiFileManager,uniPopup},
		data() {
			return {
				index: 0,
				array: [],	//['新增', '续贷', '收回再贷']
				arrayInfo:[],
				
				index2:0,
				array2:[],// ['请选择客户类型', '企业法人','事业单位','其他经济组织','农民专业合作社','金融机构','农业产业化企业龙头']
				array2Info:[],
				
				index3: 0,
				array3: [],	//['请选择营销责任人', ]
				array3Info:[],
				index3_1: 0,
				array3_1: [],	//['请选择营销责任人', ]
				array3_1Info:[],
				
				index4: 0,
				array4: [],	// ['请选择评级模型','大中型企业','小企业','新建企业','新开户法人','集团客户','事业单位','新建小企业' ]
				array4Info:[],
				
				index5: 0,
				array5: [],// ['请选择拟初评结果','AAA','AA','A','BBB','BB','B','CCC','CC','C' ]
				array5Info:[],
				
				index6: 0,
				array6: [],//['请选择上季度拟初评结果','AAA','AA','A','BBB','BB','B','CCC','CC','C' ]
				array6Info:[],
				
				index7: 0,
				array7: [],	//'请选择协助调查岗',[]
				array7Info:[],
				index7_1: 0,
				array7_1: [],	//['请选择营销责任人', ]
				array7_1Info:[],
				
				isdis:false,
				businessData:{
					title:"",				// 申报名称
					businessName:"评级认定审批",		//业务名称
					businessMechanism:"孝义农商行全辖汇总",	//   业务机构
					symboltablecode:"评级",			//业务类型
					customer_name:"",				//客户名称
					customer_id:"",					//客户号
					cust_type:"",					//客户类型
					controlPer:"",					//实际控制人
					evaluate_modle:"",		//评级模型
					first_level:"",			//拟初评结果
					applay_date:"",			//申请时间
					applay_reason:"",		//申请理由
					userIds:"",				//协助调查岗
					userIds2:"",			//营销责任人
					lastLevel:"",			//上季度评级级别
				},
				type: '',
				getUserInfo:[],
				list: [],
				path:[],
				// 保存完返回的userIds
				userIds:"",
				seq:"",
				ishold:false,
			};
		},
		onNavigationBarButtonTap(e) {
			this.isKeep();
		},
		onLoad(){
			this.businessData.applay_date = formateDate(new Date(),"Y-M-D");
			var that = this;
			uni.request({
				url:"http://192.168.3.125:8080/ams/system/distribute.htm?module=pullDown&XLX=sbType",
				success(res){
					var data = res.data;
					that.arrayInfo = data.object;
					data = data.object.map((item)=>{
						return item.name
					})
					that.array = data;
					that.businessData.title = that.arrayInfo[0].code
				}
			})
			uni.request({
				url:"http://192.168.3.125:8080/ams/system/distribute.htm?module=pullDown&XLX=customerType",
				success(res){
					var data = res.data;
					that.array2Info = data.object;
					data = data.object.map((item)=>{
						return item.name
					})
					data.unshift("请选择客户类型")
					that.array2 = data;
				}
			})
			uni.request({
				url:"http://192.168.3.125:8080/ams/system/distribute.htm?module=pullDown&XLX=level_modle",
				success(res){
					var data = res.data;
					that.array4Info = data.object;
					data = data.object.map((item)=>{
						return item.name
					})
					data.unshift("请选择拟初评结果")
					that.array4 = data;
				}
			})
			uni.request({
				url:"http://192.168.3.125:8080/ams/system/distribute.htm?module=pullDown&XLX=levelType",
				success(res){
					var data = res.data;
					that.array6Info = that.array5Info = data.object;
					data = data.object.map((item)=>{
						return item.name
					})
					var data2 = [].concat(data);
					data.unshift("请选择拟初评结果")
					data2.unshift("请选择上季度拟初评结果")
					that.array5 = data;
					that.array6 = data2;
				}
			})
			uni.request({
				url:"http://192.168.3.125:8080/ams/system/distribute.htm?module=check_dept",
				success(res){
					var data = res.data;
					that.array3Info = that.array7Info = data.object;
					var dataInfo = data.object.map((item)=>{
						return item.dept_name
					})
					var data2Info = [].concat(dataInfo);
					dataInfo.unshift("请选择营销责任人")
					that.array3 = dataInfo;
					
					data2Info.unshift("请选择协助调查岗")
					that.array7 = data2Info;
				}
			})
		},
		beforeDestroy(){
			return false
		},
		
		methods:{
			bindPickerChange: function(e) {
				this.index = e.target.value;
				this.businessData.title = this.arrayInfo[e.target.value].code;
				uni.showToast({
					title:"选择成功",
				})
				console.log(this.businessData.title)
			},
			bindPickerChange2: function(e) {
				this.index2 = e.target.value;
				if(e.target.value == 0){
					uni.showToast({
						title:"选择失败",
						icon:"none"
					})
					return;
				}
				this.businessData.cust_type = this.array2Info[e.target.value - 1].code;
				uni.showToast({
					title:"选择成功",
				})
			},
			bindPickerChange3: function(e) {
				var that = this;
				this.index3 = e.target.value;
				if(e.target.value == 0){
					uni.showToast({
						title:"选择失败",
						icon:"none"
					})
					return ;
				}
				uni.request({
					url:`http://192.168.3.125:8080/ams/system/distribute.htm?module=check_person&dept_id=${that.array3Info[e.target.value].id-1}`,
					success(res){
						var data = res.data;
						if(data.object.length == 0){
							uni.showToast({
								title:"选择失败，请重新选择！",
								icon:"none"
							})
							that.index3 = 0;
							that.array3_1 = [];
							that.businessData.userIds2 = "";
						}else{
							that.array3_1Info = data.object;
							uni.showToast({
								title:"请选择具体信息",
								icon:"none"
							})
							var dataInfo = data.object.map((item)=>{
								return item.dept_name
							})
							that.array3_1 = dataInfo;
							that.businessData.userIds2 = String(that.array3_1Info[that.index3_1].id);
						}
					}
				})
			},
			bindPickerChange3_1: function(e) {
				this.index3_1 = e.target.value;
				this.businessData.userIds2 = String(this.array3_1Info[this.index3_1].id);
				uni.showToast({
					title:"选择成功",
				})
				
			},
			bindPickerChange4: function(e) {
				this.index4 = e.target.value
				if(e.target.value == 0){
					uni.showToast({
						title:"选择失败",
						icon:"none"
					})
					return;
				}
				this.businessData.evaluate_modle = this.array4Info[e.target.value - 1].code;
				uni.showToast({
					title:"选择成功",
				})
			},
			bindPickerChange5: function(e) {
				this.index5 = e.target.value;
				if(e.target.value == 0){
					uni.showToast({
						title:"选择失败",
						icon:"none"
					})
					return;
				}
				this.businessData.first_level = this.array5Info[e.target.value - 1].code;
				uni.showToast({
					title:"选择成功",
				})
			},
			bindPickerChange6: function(e) {
				this.index6 = e.target.value;
				if(e.target.value == 0){
					uni.showToast({
						title:"选择失败",
						icon:"none"
					})
					return;
				}
				this.businessData.lastLevel = this.array6Info[e.target.value - 1].code;
				uni.showToast({
					title:"选择成功",
				})	
			},
			bindPickerChange7: function(e) {
				var that = this;
				this.index7 = e.target.value;
				if(e.target.value == 0){
					uni.showToast({
						title:"选择失败",
						icon:"none"
					})
					return ;
				}
				uni.request({
					url:`http://192.168.3.125:8080/ams/system/distribute.htm?module=check_person&dept_id=${that.array3Info[e.target.value].id-1}`,
					success(res){
						var data = res.data;
						if(data.object.length == 0){
							uni.showToast({
								title:"选择失败，请重新选择！",
							})
							that.array7_1 = [];
							that.businessData.userIds = "";
						}else{
							that.array7_1Info = data.object;
							uni.showToast({
								title:"请选择具体信息",
								icon:"none"
							})
							var dataInfo = data.object.map((item)=>{
								return item.dept_name
							})
							that.array7_1 = dataInfo;
							that.businessData.userIds = String(that.array7_1Info[that.index7_1].id);
						}
					}
				})
			},
			bindPickerChange7_1: function(e) {
				this.index7_1 = e.target.value;
				this.businessData.userIds = String(this.array7_1Info[this.index7_1].id);
				uni.showToast({
					title:"选择成功",
				})
			},
			
			togglePopup(type) {
				this.type = type;
			},
			// 提交受理
			submitAcce(){
				if(!this.ishold){
					uni.showModal({
						title: '您还没有保存',
						content: '您还没有保存数据，请先保存！',
						success: function (res) {
							if (res.confirm) {
								console.log('用户点击确定');
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						},
					})
				}else{
					var that = this;
					uni.showLoading({
						title:"提交中",
						mask:true
					})
					uni.request({
						url:`http://192.168.3.125:8080/ams/system/distribute.htm?module=submitApp&id=${that.seq}&userIds=${that.userIds}`,
						success(res){
							var data = res.data;
							if(data.code == 1){
								uni.hideLoading()
								uni.showToast({
									title:data.message,
									duration:3000
								})
								setTimeout(function(){
									uni.navigateBack();
								},3000)
							}
						},
						fail(err){
							console.log(err)
						}
					})
				}
			},
			
			
			getName(item,index){
				this.businessData.customer_name = this.getUserInfo[index].customer_name;
				this.businessData.customer_id = this.getUserInfo[index].customer_id;
				this.type = '';
				this.isdis = true
			},
			openFile(){
				this.$refs.filemanager._openFile()
			},
			resultPath(e){
				this.path.push(e);
			},
			
			delList(e){
				this.path.splice(e.target.dataset.value, 1)
			},
			// 用户名检索
			retrieval(){
				var that = this;
				if(this.businessData.customer_name == ""){
					uni.showToast({
						title:"请输入客户名称",
						duration: 2000,
						icon:'none'
					})
				}else{
					uni.showLoading({
						title: '加载中',
						mask:true
					});
					uni.request({
						url:`http://192.168.3.125:8080/ams/system/distribute.htm?module=checkCustomer&customerName=${that.businessData.customer_name}`,
						success(res){
							var data = res.data
							that.getUserInfo = data.object
							var dataInfo = data.object.map((item)=>{
								return item.customer_name
							})
							uni.hideLoading();
							that.type = "middle-list";
							that.list = dataInfo
// 							uni.showActionSheet({
// 								itemList: dataInfo,
// 								success: (e) => {  
// 									that.isdis = true
// 									that.businessData.customer_name = data.object[e.tapIndex].customer_name
// 									that.businessData.customer_id = data.object[e.tapIndex].customer_id
// 								}
// 							})
						},
						fail(err){
							console.log(err);
							uni.hideLoading()
						}
					})
				}
			},
			isKeep(){
				if(this.businessData.customer_name == ""){
					uni.showToast({
						title:"请检索客户名称",
						icon:"none"
					})
					return;
				}
				if(this.businessData.customer_id == ""){
					uni.showToast({
						title:"请检索客户id",
						icon:"none"
					})
					return;
				}
				if(this.businessData.cust_type == ""){
					uni.showToast({
						title:"请选择客户类型",
						icon:"none"
					})
					return;
				}
				if(this.businessData.controlPer == ""){
					uni.showToast({
						title:"请填写实际控制人",
						icon:"none"
					})
					return;
				}
				if(this.businessData.userIds2 == ""){
					uni.showToast({
						title:"请选择营销责任人",
						icon:"none"
					})
					return;
				}
				if(this.businessData.evaluate_modle == ""){
					uni.showToast({
						title:"请选择评级模型",
						icon:"none"
					})
					return;
				}
				if(this.businessData.first_level == ""){
					uni.showToast({
						title:"请选择拟初评结果",
						icon:"none"
					})
					return;
				}
				if(this.businessData.lastLevel == ""){
					uni.showToast({
						title:"请选择上季度拟初评结果",
						icon:"none"
					})
					return;
				}
				if(this.businessData.applay_date == ""){
					uni.showToast({
						title:"请选择审评时间",
						icon:"none"
					})
					return;
				}
// 				if(this.businessData.applay_reason == ""){
// 					uni.showToast({
// 						title:"请填写审评理由",
// 						icon:"none"
// 					})
// 					return;
// 				}
				if(this.businessData.userIds == ""){
					uni.showToast({
						title:"请选择协助调查岗",
						icon:"none"
					})
					return;
				}
				
				
				let uploadData = this.businessData;
				uploadData.user_id = String(getUserInfo().userId);
				uploadData.bus_code = "";
				delete uploadData.businessMechanism;
				delete uploadData.businessName;
				// delete uploadData.symboltablecode;
				var url = `http://192.168.3.125:8080/ams/system/distribute.htm?module=saveBusiness&business={user_id:"${uploadData.user_id}", title:"${uploadData.title}",symboltablecode:"${uploadData.symboltablecode}",customer_name:"${uploadData.customer_name}",customer_id:"${uploadData.customer_id}",cust_type:"${uploadData.cust_type}",bus_code:"${uploadData.bus_code}",evaluate_modle:"${uploadData.evaluate_modle}",first_level:"${uploadData.first_level}",applay_date:"${uploadData.applay_date}",applay_reason:"${uploadData.applay_reason}",userIds:"${uploadData.userIds}",userIds2:"${uploadData.userIds2}",lastLevel:"${uploadData.lastLevel}",controlPer:"${uploadData.controlPer}"}`
				console.log(url)
				var that = this;
				uni.showModal({
					title: '是否保存',
					content: '您还没有保存数据，是否保存？',
					success: function (res) {
						if (res.confirm) {
							uni.showLoading({
								title: '加载中',
								mask:true
							})
							uni.request({
								url:url,
								success(res){
									var data = res.data;
									if(data.code == 1){
										uni.showToast({title:`${data.message},请及时提交!`,mask:true,duration:2000})
										that.userIds = data.object.userIds;
										that.ishold = true;
										that.seq = data.object.seq;
									}
									uni.hideLoading()
								},
								fail(err){
									console.log(err);
									uni.hideLoading()
								}
							})
						} 
					}
				});
			}
		}
	}
